@use "./base/colors" as *;
@use "./base/reset" as *;
@use "./layout/header" as *;
@use "./layout/navbar" as *;
@use "./abstracts/mixins" as *;
@use "./pages/services" as *;
@use "./pages/about" as *;
@use "./pages/process" as *;
@use "./pages/faq" as *;
@use "./pages/quote" as *;
@use "./pages/contact" as *;
@use "./layout/footer" as *;



.section-padding{
    padding:6vh 0;
}
.section__box{
    display: flex;
    justify-content: center;
    flex-direction: column;
    text-align: center;
    &-heading{
        font-size: clamp(2.8rem, 1.4667rem + 3.7037vw, 4.8rem);
    }
    &-text{
        font-size: clamp(1.6rem, 1.0667rem + 1.4815vw, 2.4rem);
    }
}



















@media (min-width: 576px) { 
    .section__box{
        text-align: left;
    }
 }

@media (min-width: 768px) { 
    .header{
        &__box{
            text-align: center;
            &-heading{
                & span{
				transform: rotate(-3deg);

                }
            }
        }
        &__buttons{
            flex-direction: row;
        }
    }
 }

@media (min-width: 992px) {  
    .header{
	background-image: url("../../../dist/img/header_big.png");

    }
}

